import React, {useEffect, useRef, useState} from 'react';
import './App.css';
import openSocket from 'socket.io-client'

function parseJson<D, R>(fn: (data: D) => R) {
  return (data: string) => fn(JSON.parse(data))
}

interface Iconnect {
  to: string
  from: string
  desc: RTCSessionDescription
}
interface IconnectIces {
  to: string,
  from: string,
  ices: RTCIceCandidateInit[]
}

const wsClient = openSocket("https://549987-cb54970.tmweb.ru/", {
  transports: ['websocket'],
});
(window as any).wsClient = wsClient

function App() {
  const audio = useRef<HTMLAudioElement | null>(null)

  const createMainRadio = async () => {
    const stream
      = await navigator
        .mediaDevices
        .getUserMedia({
          audio: {
            'channelCount':{'exact': 2},
            'echoCancellation':false,
            'autoGainControl':false,
            'noiseSuppression':false
          },
          video: false
        })

    const peers: Record<string, RTCPeerConnection> = {}
    const handleConnect
      = async (id: string) => {
        const pc = new RTCPeerConnection({
          'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}]
        });
        (window as any).pc = pc;
        console.log('startConnect', pc.connectionState)
        pc.addEventListener('connectionstatechange', _ => {
          console.log(pc.connectionState);
          if (pc.connectionState === "closed") {
            delete peers[id]
          }
        })
        peers[id] = pc;
        stream.getAudioTracks().forEach(
          (track) => pc.addTrack(track)
        )
        const offer = await pc.createOffer()
        await pc.setLocalDescription(offer)
        wsClient.emit("connectDesc", {
          to: id,
          from: wsClient.id,
          desc: (offer as any).toJSON()
        })
      }
    wsClient.on('connectToRadio', handleConnect)

    const handleConnectAns = parseJson(async ({from, desc}: Iconnect) => {
      await peers[from].setRemoteDescription(desc)
    })
    wsClient.on("connectAns", handleConnectAns)

    const handleConnectToIces = parseJson(async ({from, ices}: IconnectIces) => {
      await Promise.all(
        ices.map((ice) => peers[from].addIceCandidate(ice))
      )
      await peers[from].addIceCandidate(null as any)
    })
    wsClient.on("connectToIces", handleConnectToIces)

    wsClient.emit('createRadio')
  }
  const receiveRadio = async (id: string) => {
    const pc = new RTCPeerConnection({
      'iceServers': [{'urls': 'stun:stun.l.google.com:19302'}]
    })

    const handleConnectDesc = parseJson(async ({from, desc}: Iconnect) => {
      await pc.setRemoteDescription(desc);
      (window as any).pc = pc;
      const answer = await pc.createAnswer()
      debugger
      console.log(answer)
      await pc.setLocalDescription(answer)
      wsClient.emit('connectAns', {
        to: id,
        from: wsClient.id,
        desc: (answer as any).toJSON()
      })

      const ices
        = await (new Promise((res) => {
        const ices: RTCIceCandidateInit[] = [];
        pc.onicecandidate = (event) => {
          if(event.candidate !== null) {
            ices.push(event.candidate.toJSON())
          } else {
            res(ices)
          }
        }
      }))
      wsClient.emit("connectToIces", {
        to: from,
        from: wsClient.id,
        ices
      })

      wsClient.off('connectDesc', handleConnectDesc)
    })
    wsClient.on('connectDesc', handleConnectDesc)

    const handleConnectFromIces = parseJson(async ({ices}: IconnectIces) => {
      console.log("remoteIces", ices)
      await Promise.all(
        ices.map(ice => pc.addIceCandidate(ice))
      )
      await pc.addIceCandidate(null as any)
      wsClient.off('connectFromIces', handleConnectFromIces)
    })
    wsClient.on('connectFromIces', handleConnectFromIces)

    const handleConnectionstatechange = () => {
      console.log(pc.connectionState);
      switch (pc.connectionState) {
        case "connected": {
          if(audio && audio.current) {
            debugger
            audio.current.srcObject = new MediaStream(
              pc.getTransceivers().map(({receiver: {track}}) => track)
            )
          }
          break
        }
        case "closed": {
          if(audio && audio.current) {
            audio.current.srcObject = null
          }
          pc.removeEventListener("connectionstatechange", handleConnectionstatechange)
        }
      }
    }
    pc.addEventListener('connectionstatechange', handleConnectionstatechange)

    wsClient.emit('connectToRadio', id)
  }

  const [radios, setRadios] = useState<string[]>([]);
  useEffect(() => {
    wsClient.on('activeRadios', parseJson((radios: string[]) => {
      console.log('activeRadios', radios)
      setRadios(radios)
    }))
    wsClient.on("createRadio", (radio: string) => {
      setRadios(radios => [...radios, radio])
    })
    wsClient.on("deleteRadio", (radio: string) => {
      setRadios(radios => radios.filter(
        (storeRadio) => storeRadio !== radio)
      )
    })
  }, [])

  return (
    <div className="App">
      <audio ref={audio} controls>
      </audio>
      <div>
        {radios.map(id => <button
          onClick={() => receiveRadio(id)}
          key={id}
        >
          {id}
        </button>)}
      </div>
      <button onClick={createMainRadio}>create main radio</button>
    </div>
  );
}

export default App;
